import React from 'react'
import ReactDom from 'react-dom'

import $ from '../../lib/jquery.js'
import style from './studyTrends.css'

class StudyTrends extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			data: "",
			pageNum: 1
		};
	}

	page() {
		let getPage = {
			oContent: $('.content'), // 
			oLists: '', // 
			oStart: $('.start'), // 
			oLast: $('.last'), //
			oPrev: $('.prev'), // 
			oNext: $('.next'), // 
			pageLen: 5, // 
			pageNum: 0, // 
			index: 0, // 
			oUl: $('.page ul'),
			oUlLi: '', // 0
			oSpan: $('.page span'), // 
		};
		// (1~3)
		( () => {
			let promise = new Promise((resolve,reject)=>{
				$.ajax({
					type: "post",
					url: "http://rap.taobao.org/mockjsdata/9989/strudyTrends",
					dataType: "json",
					success: (data) => {
						if(data.status!=0){
							resolve(data);
						} else {
							reject(new Error("!"));
						}
					},
					error: () => {
						reject(new Error("!"));
					}
				})
			})
			promise.then((data)=>{
				initPage(data);
				addPageNum();
				showPage(0);
				// 
				$(getPage.oUlLi).click(function(){
					getPage.index = $(this).index();
					showPage(getPage.index);
				});
			}).catch((value)=>{
				alert(value);
			});
		})()
		// 
		const addPageNum = () => {
			let str = '';
			for(var i = 1;i <= getPage.pageNum;i++){
				str += '<li>'+i+'</li>';
			}
			getPage.oUl.html(str);
			getPage.oUlLi = getPage.oUl.find('li');
		}

		// 
		const initPage = (data) => {
			let initPageCon = data.data,
				aStr = '';

			initPageCon.map((item) => {
				aStr += "<div class='items'>" + 
						"	<h2 class='items-tit'>" + 
								item.title1 +	
						"	</h2>" + 
						"	<div>" + 
								item.content1 + 
						"	</div>" + 
						"</div>";
			})
			$(getPage.oContent).html(aStr); // 
			getPage.oLists = $(getPage.oContent).find('.items'); // 
			let oListsLen = getPage.oLists.length; // 
			getPage.pageNum = Math.ceil(oListsLen/getPage.pageLen); // 
			$('.allNum').html(""+getPage.pageNum+"");
		}

		// 
		const isFirstLast = () => {
			if(getPage.index == 0){
				getPage.oPrev.addClass('forbid');
				getPage.oStart.addClass('forbid');
				getPage.oNext.removeClass('forbid');
				getPage.oLast.removeClass('forbid');
				return false;
			}
			else if(getPage.index == (getPage.pageNum - 1)){
				getPage.oNext.addClass('forbid');
				getPage.oLast.addClass('forbid');
				getPage.oPrev.removeClass('forbid');
				getPage.oStart.removeClass('forbid');
				return false;
			} else {
				getPage.oSpan.removeClass('forbid');
			}
		}

		// 
		getPage.oNext.click( () => {
			if(getPage.index == (getPage.pageNum - 1)){
				alert('!');
				return false;
			}
			getPage.index +=1;
			showPage(getPage.index);
		})
		// 
		getPage.oPrev.click( () => {
			if(getPage.index == 0){
				alert('!');
				return false;
			}
			getPage.index -=1;
			showPage(getPage.index);
		})

		// 
		getPage.oStart.click( () => {
			if(getPage.index == 0){
				alert('!');
				return false;
			}
			getPage.index = 0;
			showPage(getPage.index);
		})
		// 
		getPage.oLast.click( () => {
			if(getPage.index == (getPage.pageNum - 1)){
				alert('!');
				return false;
			}
			getPage.index = getPage.pageNum - 1;
			showPage(getPage.index);
		})
		// 
		const showPage = (num) => {
			$(getPage.oLists).hide();
			for(var i = getPage.index * getPage.pageLen;i < (getPage.index + 1) * getPage.pageLen;i++){
				$(getPage.oLists[i]).css({"display":"block"});
			}
			$(getPage.oUlLi).eq(num).addClass('active').siblings().removeClass('active');
			isFirstLast();
		}
	}

	componentDidMount() {
		this.page()
	}

	render() {
		return (
			<div className={style.main}>
				<div className={style.main_wrap}>
					<div className={style.title}>
						<h2 className={style.title1}></h2>
					</div>
					<div className={style.container}>
						<div className={style.content}>
							<div className={style.items}>
								<h2 className={style.items_tit}></h2>
								<div className={style.items_con}>{this.state.data}</div>
							</div>
						</div>
						<div className={style.page_container}>
							<div className={style.page}>
								<span className={style.start}></span>
								<span className={style.prev}></span>
								<ul className={style.pageNum}></ul>
								<span className={style.next}></span>
								<span className={style.last}></span>
								<div className={style.allNum}>{this.state.pageNum}</div>
							</div>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

module.exports = StudyTrends


