import React from 'react'

import $ from '../../lib/jquery.js'
import style from './index.css'

class Home extends React.Component{
	constructor(props) {
		super(props);
		this.state = {
			studyTrendsData: [],
			studyActivityData: []
		};
	}

	getData() {
		// 
		(() => {
			$.ajax({
				type: "get",
				url: "http://rap.taobao.org/mockjsdata/9989/index",
				dataType: "json",
				success: (data) => {
					if(data.status!=0){
						this.setState({
							studyTrendsData: data.data
						})
					} else {
						alert("!");
					}
				},
				error: () => {
					alert("!");
				}
			})
		})();

		// 
		(() => {
			let promise = new Promise((resolve,reject) => {
				$.ajax({
					type: "get",
					url: "http://rap.taobao.org/mockjsdata/9989/index",
					dataType: "json",
					success: (data) => {
						if(data.status!=0){
							this.setState({
								studyActivityData: data.data
							})
						} else {
							reject(new Error("!"));
						}
					},
					error: () => {
						reject(new Error("!"));
					}
				})
			})
		})()
	}

	componentWillMount() {
		this.getData()
	}

	render(){
		// 
		let str = this.state.studyTrendsData;
		let A = str.map((items)=>{
			return <li>
				   		<a href='#'>{items.items}
				   			<b class='date1'>{items.date}</b>
				   		</a>
				   	</li>;
		});
		// 
		let iStr = this.state.studyActivityData;
		let B = iStr.map((items)=>{
			return <li>
				   		<a href='#'>{items.items}
				   			<b class='date1'>{items.date}</b>
				   		</a>
				   	</li>;
		});
		
		return (
			<div className={style.container}>
				<div className={style.container_wrap}>
					<div className={style.sidebar}>
						<div className={style.section}>
							<div className={style.title}>
								<h2></h2>
								<div className={style.more}>
									<a href="#"></a>
								</div>
							</div>
							<div className={style.item}>
								<ul className={style.studyTrendsItem} id="studyTrendsItem">{A}</ul>
							</div>
						</div>
						<div className={style.section4} className={style.mt14}>
							<div className={style.title}>
								<h2></h2>
								<div className={style.more}>
									<a href="#"></a>
								</div>
							</div>
							<div className={style.item}>
								<ul className={style.studyActivityItem} id="studyActivityItem">{B}</ul>
							</div>
						</div>
						
					</div>
					<div className={style.main}>
						<div className={style.main_title}>
							<h2></h2>
						</div>
						<div className={style.about_pic}>
							<img src="./img/index-about1.png" alt="" />
						</div>
						<div className={style.about_us_article}>
							<p>
								
							</p>
							<p>
								
							</p>
						</div>
						<div className={style.more1}>
							<a href="#"></a>
						</div>
					</div>
				</div>
			</div>
		)
	}
}

module.exports = Home


