import React from 'react'
import { Link, IndexLink } from 'react-router'

import style from './common.css'

class Footer extends React.Component{
	render(){
		return (
			<div className={style.fooWrap}>
				<div className={style.footer}>
				    <div className={style.friLink}>
				        <h3 className={style.friLinkTit}></h3>
				        <div className={style.friLinkCon}>
				            <div className={style.friLinkCul}>
				                <Link className={style.firLinkItem} href='#'></Link>
					            <Link className={style.firLinkItem} href='#'></Link> 
				            </div>
				            <div className={style.friLinkCul}>
				                <Link className={style.firLinkItem} href='#'></Link>
					            <Link className={style.firLinkItem} href='#'></Link>
				            </div>
				            <div className={style.friLinkCul}>
				                <Link className={style.firLinkItem} href='#'></Link>
					            <Link className={style.firLinkItem} href='#'></Link>
				            </div>
				            <div className={style.friLinkCul}>
				                <Link className={style.firLinkItem} href='#'></Link>
					            <Link className={style.firLinkItem} href='#'></Link>
				            </div>
				        </div>
				    </div>
				    <div className={style.relLink}>
				        <div className={style.address}>
				            <span>|</span><span>|</span>2<span>|</span>400065<span>|</span>ICP:10005091-2
				        </div>
				        <div className={style.extraLink}>
				            <Link className={style.exLinkItem} href='#'></Link>
				            <span>|</span>
				            <Link className={style.exLinkItem} href='#'></Link>
				        </div>
				        <p className={style.copyright}>CopyRight  2016</p>
				    </div>
				</div>
			</div>
		)
	}
}

module.exports = Footer


